.form {
    width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgb(24, 25, 29);
}

.form form {
    width: 350px;
    display: flex;
    flex-direction: column;
    border: 1px solid white;
}

.form input {
    color: white;
    height: 3rem;
    width: 100%;
    font-size: 1rem;
    padding-right: 1rem;
    border:none;
    background-color: transparent;
}

.form form img {
    width: 1.5rem;
    height: 1.5rem;
    margin: 0 10px;
}

.name, .mail, .pass {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #adadad;
}


.btn {
    padding: 0 15px;
}

.form button {
    width: 100%;
    padding: 11px 10px;
    border-radius: 3px;
    margin: 18px auto;
    font-size: 1.1rem;
    font-weight: bold;
    border: 0 none;
    background-color: #2b88d9;
    color: #fff;
    cursor: pointer;
}

.form input:focus, .form button:focus {
    outline: none;
}

.form button:hover {
    background-color: #4491d4;
}

.social-login{
    margin: 15px;
    width: 350px;
    display: flex;
    justify-content: space-between;
}

.social-login a{
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 2rem;
    color: #fff;
}

.social-login a:nth-child(1){
    background: #000;
}

.social-login a:nth-child(2){
    background: red;
}

.form p{
    width: 350px;
    font-size: 1.1rem;
    text-align: center;
    color: white;
}

.form p a {
    color: #1b7edf;
    text-decoration: none;
    font-weight: bold;
    font-size: 1.2rem;
}

.form ::-webkit-input-placeholder{
	color: #fff;
}
.form :-moz-placeholder{
	color: #fff;
}
.form ::-moz-placeholder{
	color: #fff;
}
.form ::-ms-input-placeholder{
	color: #fff;
}