.username{
    font-size: 0.6rem;
    margin: 0 15px;
}
.username a{
    font-size: 0.7em;
    display: block;
    padding-top: 10px;
    color:#ffffff;
}
.profile, .login{
    position: absolute;
    right:  -100%;
    transform: translateY(-50%);
    color:#e0e0e0;
}
.userLogin{
    position: relative;
}

.login:hover, .language:hover{
    color: white;
}

.profile a:hover{
    color: red;
}

.login{
	display: block;
	padding: 0 10px;
}
.navbar-container{
	margin: 0 auto;
	display: flex;
  width: 100%;
  max-width: 1980px;
  padding: 10px 30px;
	justify-content: space-between;
}
.logo-header{
	display: flex;
  align-items: center;
}
.right-social-menu{
  display: flex;
  align-items: center;
  padding-right: 15px;
  width: 100%;
}
.social-icon i{
	color: #808080;
	margin: 0;
	padding: 0;
}
.icon{
	position: relative;
	cursor: pointer;
	width: 20px;
	height: 20px;
	text-align: center;
	margin: -1px 0 0 5px;
	padding: 0;
	line-height: 20px;
}
.social-icon{
  display: flex;
  flex-direction: row-reverse;
  margin-left: 5px;
}

a>i:hover{
	color:#d8d8d8;
	text-decoration: none;
}
.main-menu{
	width: auto;
	float: right;
	display: block !important;
}
.list-menu{
	padding-left: 7%;
  display: flex;
  justify-content: center;
  position: relative;
	width: 100%;
}
.item-menu{
	position: relative;
	display: block;
	line-height: 13px;
	margin-left: 0;
	margin: 0;
	float: left;
}
.href-menu{
	color: #e0e0e0;
	margin: 0;
	text-decoration: none;
	padding: 15px 20px;
	display: block;
	font-family: 'Hind', 'Arial', sans-serif;
	font-weight: 500;
	font-size: 12px;
	text-transform: uppercase;
	letter-spacing: 3px;
	line-height: 20px;
}
.href-menu:hover{
	color:#fff;
}

.upper{
	display: block ! important;
	position: fixed ! important;
	z-index: 999;
}