* {
  box-sizing: border-box;
  padding: 0;
	margin: 0;
}

body{
	font-family: 'Muli', 'Arial' ,sans-serif;
	font-weight: normal;
	font-size: 14px;
  color: #595959;
}

a{
	text-decoration: none;
}






.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.vertical-line{
	display: flex;
	justify-content: center;
}
.bord{
	height: 40px;
	width: 2px;
	border:none;
	border-right: 1px solid rgb(155, 155, 155);
}
h3,h6{
	text-align: center;
	color: #303030;
	text-transform: uppercase;
	margin-top: 0;
}
h6 {
	font-weight: 600;
	font-size: 12px;
	margin-bottom: 10px;
	letter-spacing: 3px;
}
h3{
	font-weight: 400;
	font-size: 50px;
	letter-spacing: 10px;
	margin-bottom: 30px;
}
#about-list{
	width: 100%;
	position: relative;
	background: -webkit-linear-gradient(rgba(11, 11, 9, 1) 0%,white 20%,white 80%,rgba(0, 0, 0, 1) 100%);
	background: linear-gradient(rgba(11, 11, 9, 1) 0%,white 20%,white 80%,rgba(0, 0, 0, 1) 100%);
}
.about{
	text-align: center;
	width: 50%;
	margin: 0 auto;
	line-height: 1.75em;
}
.title-about{
	max-width: 1920px;
	padding: 6rem 0;
}

.about-us {
	padding: 40px 0;
}

.choose {
	text-align: center;
	width: 100vw;
	padding: 20vh 0;
}

.choose-project{
	max-width: 1000px;
	margin: 0 auto;
}
.question {
	font-family: 'Hind', 'Arial', 'sans-serif';
    font-size: 35px;
    color: #ffffff;
    text-transform: uppercase;
    font-weight: 300;
	letter-spacing: 3px;
    -webkit-margin-after: 1.33em;
            margin-block-end: 1.33em;
}
.projects a{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-size: 20px;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 4px;
    font-family: 'Hind', 'Arial', sans-serif;
    font-weight: 300;
    line-height: 20px;
}
.description {
	-webkit-margin-before: 1em;
	        margin-block-start: 1em;
}
.slick-next, .slick-prev {
	width: 30px;
	height: 30px;
}
.slick-next::before, .slick-prev::before {
	font-size: 30px;
}
