.slick-slider {
	display: flex;
	margin-right: 15px;
	align-items: center;
	width: 100%;
}

#partners-list {
    width: 100vw;
}

.wrapper-list {
    max-width: 1000px;
    margin: 0 auto;
    padding: 6rem 0;
}
.company {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 3rem 0;
}

.company a {
    display: inline-block;
    width: 30%;
}

.company img {
    width: 100%;
    padding-right: 60px;
}

.company p {
    display: inline-block;
    width: 70%;
}

.title-img{
	position: relative;
	top: 50vh;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: #fff;
	text-align: center;
}
h1{
	font-size: 31px;
	text-transform: uppercase;
	color: #bcbcbb;
	font-weight: 500;
	letter-spacing: 3px;
	padding-left: 20px;
}
.logo{
	width: 122px;
	height: 21px;
	position: relative;
	top:10px;
}

header{
	background: url(/img/header.jpg) no-repeat center center;
	background-size: cover;
	width: 100%;
 	min-height: 100vh;
}

.wrapper {
	width: 100vw;
	position: fixed;
	top:0;
	background-color: rgba(0, 0, 0, .6);
	z-index: 1;
}
.title-name {
	padding: 30px 0;
	display: flex;
	flex-direction: row;
	justify-content: center;
  }
.username{
    font-size: 0.6rem;
    margin: 0 15px;
}
.username a{
    font-size: 0.7em;
    display: block;
    padding-top: 10px;
    color:#ffffff;
}
.profile, .login{
    position: absolute;
    right:  -100%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    color:#e0e0e0;
}
.userLogin{
    position: relative;
}

.login:hover, .language:hover{
    color: white;
}

.profile a:hover{
    color: red;
}

.login{
	display: block;
	padding: 0 10px;
}
.navbar-container{
	margin: 0 auto;
	display: flex;
  width: 100%;
  max-width: 1980px;
  padding: 10px 30px;
	justify-content: space-between;
}
.logo-header{
	display: flex;
  align-items: center;
}
.right-social-menu{
  display: flex;
  align-items: center;
  padding-right: 15px;
  width: 100%;
}
.social-icon i{
	color: #808080;
	margin: 0;
	padding: 0;
}
.icon{
	position: relative;
	cursor: pointer;
	width: 20px;
	height: 20px;
	text-align: center;
	margin: -1px 0 0 5px;
	padding: 0;
	line-height: 20px;
}
.social-icon{
  display: flex;
  flex-direction: row-reverse;
  margin-left: 5px;
}

a>i:hover{
	color:#d8d8d8;
	text-decoration: none;
}
.main-menu{
	width: auto;
	float: right;
	display: block !important;
}
.list-menu{
	padding-left: 7%;
  display: flex;
  justify-content: center;
  position: relative;
	width: 100%;
}
.item-menu{
	position: relative;
	display: block;
	line-height: 13px;
	margin-left: 0;
	margin: 0;
	float: left;
}
.href-menu{
	color: #e0e0e0;
	margin: 0;
	text-decoration: none;
	padding: 15px 20px;
	display: block;
	font-family: 'Hind', 'Arial', sans-serif;
	font-weight: 500;
	font-size: 12px;
	text-transform: uppercase;
	letter-spacing: 3px;
	line-height: 20px;
}
.href-menu:hover{
	color:#fff;
}

.upper{
	display: block ! important;
	position: fixed ! important;
	z-index: 999;
}


.language{
	color:#9b9b9b;
	font-size: 12px;
	font-weight: 1000;
	padding: 0 17px;
}

.language-buttom{
	list-style: none;
	display: inline-block;
	border-left-color: #808080;
	border-left-width: 1px;
	border-left-style: solid;
	border-right-color: #808080;
	border-right-width: 1px;
	border-right-style: solid;
}


footer{
    background: -webkit-linear-gradient(white 0%,rgba(95, 95, 95, 0.5) 11%,rgba(0,0,0,.8),rgba(0,0,0,1) 100%),url(/img/contacts.jpg) no-repeat center center;
    background: linear-gradient(white 0%,rgba(95, 95, 95, 0.5) 11%,rgba(0,0,0,.8),rgba(0,0,0,1) 100%),url(/img/contacts.jpg) no-repeat center center;
    background-size: cover;
    width: 100%;
    min-height: 100vh;
}

.contact-title {
    min-height: 100vh;
    max-width: 1000px;
    margin: 0 auto;
    padding-top: 23vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.contact-title h5 {
    font-family: 'Hind', 'Arial' ,sans-serif;
    font-weight: 900;
    color: #adadad;
    font-size: 16px;
    letter-spacing: 10px;
    text-align: center;
    margin-top: 0;
}

.contact-title h3 {
    font-family: 'Hind', 'Arial', 'sans-serif';
    font-weight: 300;
    font-size: 38px;
    color: #f2f2f2;
    line-height: 42px;
}
footer .icon{
	width: 25px;
	height: 25px;
    line-height: 25px;
    font-size: 20px;
    margin: -2px 0 0 15px;
}
footer .icon:last-child{
    margin: -2px 0 0;
}
footer .social-icon {
    margin-bottom: 20px;
    margin-left: 0;
    justify-content: center;
}
footer .form {
    width: 100%;
}

.wrap input:focus,.wrap textarea:focus, .sub-mail:hover{
    outline: none;
    background: rgba(255,255,255,0.05) !important;
    border-color: #fff !important;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.row div, .p-message{
    flex: 1 1 auto;
    padding: 0 15px;
}

.row input, footer textarea{
    font-size: 0.8rem;
    font-weight: 700;
    border: 1px solid #3c3b3b;
    background: transparent;
    color: #fff;
    width: 100%;
    margin: 10px;
    padding: 12px 20px;
    resize: none;
    outline: none;
}


.sub-mail {
    background: transparent;
    font-family: "Mali";
    color: #cecece !important;
    font-weight: 600;
    font-size: 15px;
    letter-spacing: 1px;
    padding: 13px 15px 10px 15px;
    cursor: pointer;
    width: auto;
    text-transform: uppercase;
    outline: none;
}

footer .logo-header {
    justify-content: center;
    padding: 10px 0;
}

.footer-soc {
    padding-bottom: 40px;
}
footer ::-webkit-input-placeholder{
	color: #fff;
}
footer :-moz-placeholder{
	color: #fff;
}
footer ::-moz-placeholder{
	color: #fff;
}
footer ::-ms-input-placeholder{
	color: #fff;
}
.form {
    width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgb(24, 25, 29);
}

.form form {
    width: 350px;
    display: flex;
    flex-direction: column;
    border: 1px solid white;
}

.form input {
    color: white;
    height: 3rem;
    width: 100%;
    font-size: 1rem;
    padding-right: 1rem;
    border:none;
    background-color: transparent;
}

.form form img {
    width: 1.5rem;
    height: 1.5rem;
    margin: 0 10px;
}

.name, .mail, .pass {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #adadad;
}


.btn {
    padding: 0 15px;
}

.form button {
    width: 100%;
    padding: 11px 10px;
    border-radius: 3px;
    margin: 18px auto;
    font-size: 1.1rem;
    font-weight: bold;
    border: 0 none;
    background-color: #2b88d9;
    color: #fff;
    cursor: pointer;
}

.form input:focus, .form button:focus {
    outline: none;
}

.form button:hover {
    background-color: #4491d4;
}

.social-login{
    margin: 15px;
    width: 350px;
    display: flex;
    justify-content: space-between;
}

.social-login a{
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 2rem;
    color: #fff;
}

.social-login a:nth-child(1){
    background: #000;
}

.social-login a:nth-child(2){
    background: red;
}

.form p{
    width: 350px;
    font-size: 1.1rem;
    text-align: center;
    color: white;
}

.form p a {
    color: #1b7edf;
    text-decoration: none;
    font-weight: bold;
    font-size: 1.2rem;
}

.form ::-webkit-input-placeholder{
	color: #fff;
}
.form :-moz-placeholder{
	color: #fff;
}
.form ::-moz-placeholder{
	color: #fff;
}
.form ::-ms-input-placeholder{
	color: #fff;
}
