footer{
    background: linear-gradient(white 0%,rgba(95, 95, 95, 0.5) 11%,rgba(0,0,0,.8),rgba(0,0,0,1) 100%),url(/img/contacts.jpg) no-repeat center center;
    -webkit-background-size: cover;
    background-size: cover;
    width: 100%;
    min-height: 100vh;
}

.contact-title {
    min-height: 100vh;
    max-width: 1000px;
    margin: 0 auto;
    padding-top: 23vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.contact-title h5 {
    font-family: 'Hind', 'Arial' ,sans-serif;
    font-weight: 900;
    color: #adadad;
    font-size: 16px;
    letter-spacing: 10px;
    text-align: center;
    margin-top: 0;
}

.contact-title h3 {
    font-family: 'Hind', 'Arial', 'sans-serif';
    font-weight: 300;
    font-size: 38px;
    color: #f2f2f2;
    line-height: 42px;
}
footer .icon{
	width: 25px;
	height: 25px;
    line-height: 25px;
    font-size: 20px;
    margin: -2px 0 0 15px;
}
footer .icon:last-child{
    margin: -2px 0 0;
}
footer .social-icon {
    margin-bottom: 20px;
    margin-left: 0;
    justify-content: center;
}
footer .form {
    width: 100%;
}

.wrap input:focus,.wrap textarea:focus, .sub-mail:hover{
    outline: none;
    background: rgba(255,255,255,0.05) !important;
    border-color: #fff !important;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.row div, .p-message{
    flex: 1 1 auto;
    padding: 0 15px;
}

.row input, footer textarea{
    font-size: 0.8rem;
    font-weight: 700;
    border: 1px solid #3c3b3b;
    background: transparent;
    color: #fff;
    width: 100%;
    margin: 10px;
    padding: 12px 20px;
    resize: none;
    outline: none;
}


.sub-mail {
    background: transparent;
    font-family: "Mali";
    color: #cecece !important;
    font-weight: 600;
    font-size: 15px;
    letter-spacing: 1px;
    padding: 13px 15px 10px 15px;
    cursor: pointer;
    width: auto;
    text-transform: uppercase;
    outline: none;
}

footer .logo-header {
    justify-content: center;
    padding: 10px 0;
}

.footer-soc {
    padding-bottom: 40px;
}
footer ::-webkit-input-placeholder{
	color: #fff;
}
footer :-moz-placeholder{
	color: #fff;
}
footer ::-moz-placeholder{
	color: #fff;
}
footer ::-ms-input-placeholder{
	color: #fff;
}