.title-img{
	position: relative;
	top: 50vh;
	transform: translateY(-50%);
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: #fff;
	text-align: center;
}
h1{
	font-size: 31px;
	text-transform: uppercase;
	color: #bcbcbb;
	font-weight: 500;
	letter-spacing: 3px;
	padding-left: 20px;
}
.logo{
	width: 122px;
	height: 21px;
	position: relative;
	top:10px;
}

header{
	background: url(/img/header.jpg) no-repeat center center;
	background-size: cover;
	width: 100%;
 	min-height: 100vh;
}

.wrapper {
	width: 100vw;
	position: fixed;
	top:0;
	background-color: rgba(0, 0, 0, .6);
	z-index: 1;
}
.title-name {
	padding: 30px 0;
	display: flex;
	flex-direction: row;
	justify-content: center;
  }