
.vertical-line{
	display: flex;
	justify-content: center;
}
.bord{
	height: 40px;
	width: 2px;
	border:none;
	border-right: 1px solid rgb(155, 155, 155);
}
h3,h6{
	text-align: center;
	color: #303030;
	text-transform: uppercase;
	margin-top: 0;
}
h6 {
	font-weight: 600;
	font-size: 12px;
	margin-bottom: 10px;
	letter-spacing: 3px;
}
h3{
	font-weight: 400;
	font-size: 50px;
	letter-spacing: 10px;
	margin-bottom: 30px;
}
#about-list{
	width: 100%;
	position: relative;
	background: linear-gradient(rgba(11, 11, 9, 1) 0%,white 20%,white 80%,rgba(0, 0, 0, 1) 100%);
}
.about{
	text-align: center;
	width: 50%;
	margin: 0 auto;
	line-height: 1.75em;
}
.title-about{
	max-width: 1920px;
	padding: 6rem 0;
}

.about-us {
	padding: 40px 0;
}
