#partners-list {
    width: 100vw;
}

.wrapper-list {
    max-width: 1000px;
    margin: 0 auto;
    padding: 6rem 0;
}
.company {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 3rem 0;
}

.company a {
    display: inline-block;
    width: 30%;
}

.company img {
    width: 100%;
    padding-right: 60px;
}

.company p {
    display: inline-block;
    width: 70%;
}
